<template>
    <div class="model-edit">
        <form @submit.prevent="onSubmit">
            <div class="title">Edit:</div> 
            <input type="text" v-model="todoTitleFromId">
            <input type="text" v-model="dateEvent">
            <div class="error-date">Date not correct</div> 
            <button type="submit" v-on:click='ClickBtn'>Apply</button>         
        </form>  
    </div>
</template>
<script>
export default {
    props:{
        todoTitleFromId: String,
        dateEvent:String
    },
    data(){

    },
    methods:{
        ClickBtn(){
            let date = this.dateEvent
            var aTmp = date.split(".");
            if(aTmp.length!=3)
            {
                return false;
            }
    
            //Границы разрешенного периода. Нельзя ввести дату до 1990-го года и позднее 2020-го.
            if((parseInt(aTmp[2], 10)<= 1990)||(parseInt(aTmp[2], 10)>=21000))
            {
                return false;
            }
    
            var sTmp=aTmp[2] +'-'+ aTmp[1]+'-'+ aTmp[0];
    
            if(new Date(sTmp)=='Invalid Date')
            {
                const dateError = document.querySelector('.error-date')
                dateError.classList.add('active')
            }
            else
            {
                this.$emit('editTodo', this.todoTitleFromId, this.dateEvent)
                const dateError = document.querySelector('.error-date')
                const editModel = document.querySelector('.wrap-model-edit')
                editModel.classList.remove('active')
                dateError.classList.remove('active')
            }
                //this.$emit('editTodo', this.todoTitleFromId, this.dateEvent)
                //const editModel = document.querySelector('.wrap-model-edit')
                //editModel.classList.remove('active')
            }
    }
}
</script>

<style scoped>
button{
    cursor: pointer;
}
form{
    border: #5092E3 2px solid;
    position: relative;
    width: 100%;
    padding: 10px 15px;
}
input{
    padding-left: 5px;
    margin: 10px 0;
    outline: none;
    height: 40px;
    color: white;
    background-color: transparent;
    border: #5092E3 2px solid;
    width: 100%;
    border-radius: 10px;
}
button{
    color: #5092E3;
    text-transform: uppercase;
    width: 200px;
    padding: 10px 5px;
    margin: 15px 0 15px 50%;
    transform: translate(-50%);
    background: transparent;
    border: #5092E3 2px solid;
    border-radius: 10px;
    transition: 1s all;
}
button:hover{
    background: #5092E3;
    color: white;
}
.title{
    color: #5092E3;
    font-size: 28px;
    padding: 10px;
}
.model-edit{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 80%;
  background: #202225;

}
.error-date {
    display: none;
    color: red;
}
.error-date.active{
    display: block;
}

</style>